// Typography
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

@import "./mixins";

@import "./variables";

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.h {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  margin: 0 0 0.5em;
  letter-spacing: 1px;
}

.h1 {
  font-size: $h1;
  @include desktop {
    font-size: $h1-tablet-desktop;
    color: blue;
  }
  @include tablet {
    font-size: $h1-tablet-desktop;
    color: red;
  }
}
